<template lang="pug">
   v-content
    v-container(grid-list text-xs-center)
      v-layout(row wrap)
        v-flex(xs12).my-2
          p.title {{ title }}
        v-flex(xs12 md6)
          app-IllustrationBlock
        v-flex.answer-block--background.pa-2(xs12 md6)
          app-SelectAnswer
          app-AnswerMessage
          app-NextOrNew
</template>

<script>
import IllustrationBlock from '../../components/shared/IllustrationBlock'
import NextOrNew from '../../components/shared/NextOrNew'
import AnswerMessage from '../../components/shared/AnswerMessage'
import SelectAnswer from '../../components/shared/SelectAnswer'

export default {
  data() {
    return {
      problem: 's7p7',
      image: {src: require('../../assets/section7/problem7-8.jpg'), alt: 'illustration for problem 7'},
      hint: {title: 'Hint for Problem 7', type: 'textHint', text: 'Filter Area Required = Flow Rate / Filter Media Rate'},
      next: {text: 'Problem 7: Cartridge Filter - Maximum Flow Provided', route: '/cartridge-filter-max-flow-provided'},
      newButton: 'New Cartridge Filter Size Required Problem',
      answerSet: {type: 'filter', decimals: 1}
    }
  },
  components: { appIllustrationBlock: IllustrationBlock, appNextOrNew: NextOrNew, appAnswerMessage: AnswerMessage, appSelectAnswer: SelectAnswer },
  computed: {
    title() { return `Your circulation system has a flow rate of ${this.units.flow} gpm.\u00A0\u00A0You are purchasing a new cartridge filter that has a filter media rate of ${this.units.fmr} gpm/ft\u00B2.\u00A0\u00A0What is the minimum area (in ft\u00B2) that this new cartridge filter needs to be?` },
    units() { return this.$store.getters.units }
  },
  created() {
    let data = {problem: this.problem, image: this.image, hint: this.hint, next: this.next, newButton: this.newButton, answerSet: this.answerSet};
    this.$store.dispatch('init', data);
  }
}
</script>



